<template>
  <div class="rightContent">
    <div class="navTile">
      当前位置：
      <router-link :to="{name:'Main'}">首页</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Resource'}">教学资源</router-link>
      &nbsp;>>&nbsp;
      <router-link :to="{name:'Videos'}">教学视频</router-link>
    </div>
    <div class="content">
      <div class="myDetail">
        <div class="videoTitle">
          <p class="videoP">{{video.videoName}}</p>
          <div class="videoSpanDiv">
            <span class="videoSpan">
            {{video.videoTime}}
          </span>
            <span class="videoSpan">
            {{video.viewTimes}}人浏览
          </span>
          </div>
        </div>
        <div class="videoDiv" v-for="(list,index) in videos" :key="index">
          <Video
            :videoUrl="list"
            :posterUrl="video.videoImgUrl"
          ></Video>
        </div>
        <div class="videoContent">
          <div class="videoContentTitle">
            视频介绍：
          </div>
          <div id="videoIntroduce" class="videoIntroduce">
            {{setIntroduce('videoIntroduce',video.videoIntro)}}
          </div>
        </div>
        <div class="videoComment">
          <div class="videoCommentItem">
            <div class="videoCommentTitle">
              我要评论：
            </div>
            <div class="videoCommentForm">
              <el-form ref="form" :model="commentform" label-width="60px">
                <el-form-item label="姓名：">
                  <el-input v-model="commentform.commentName" placeholder="请输入姓名"></el-input>
                </el-form-item>
                <el-form-item label="内容：">
                  <el-input type="textarea"
                            v-model="commentform.commentContent"
                            :autosize="{ minRows: 3, maxRows: 4}"
                            maxlength="100"
                            show-word-limit>
                  </el-input>
                </el-form-item>
                <el-form-item>
                  <el-button type="primary" @click="uploadComment">发表评论</el-button>
                </el-form-item>
              </el-form>
            </div>
          </div>
          <div class="videoCommentItem">
            <div class="videoCommentTitle">
              评论列表：
            </div>
            <div class="videoComments" v-if="comments.commentVOS && comments.commentVOS.length > 0">
              <ul class="commentUl" v-for="(comment,index) in comments.commentVOS" :key="index">
                <li class="commenterInfo">
                  <span class="commenterName">{{comment.commentName}}：</span>
                  <span class="commenterTime">{{comment.commentTime}}</span>
                </li>
                <li class="comment">
                  {{comment.commentContent}}
                </li>
              </ul>
              <div class="page">
                <el-pagination
                  @current-change="handleCurrentChange"
                  :current-page.sync="pageNum"
                  :page-size="pageSize"
                  :hide-on-single-page="comments.pages < 2"
                  layout="total, prev, pager, next"
                  :total="comments.total">
                </el-pagination>
              </div>
            </div>
            <div class="videoComments emptyComment" v-else>
              快来抢沙发吧
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import Video from '../../../../../components/User/video/video'
  import {REQONEVIDEO,UPLOADCOMMENT} from '../../../../../api/types'
  import {mapActions} from 'vuex'
  export default {
    name: '',
    data () {
      return {
        videoId:'',            //视频Id
        pageSize:10,           //评论行数
        pageNum:1,             //评论页数
        videos:[],             //视频列表
        video:{},              //当前视频对象

        commentform:{
          commentName:'',      //评论者姓名
          commentContent:'',   //评论内容
        },
        comments:[],           //评论数组
      }
    },
    components:{
      Video,
    },
    methods: {
      ...mapActions(['choiceMenuId','choiceTypeMenuId']),

      //分页
      handleCurrentChange(num){
        this.pageNum = num
        this.reqVideo(true)
      },

      //获取视频,isGetComment标识是否只是更新评论
      async reqVideo(isGetComment = false){
        var {videoId,pageNum,pageSize} = this
        var result = await REQONEVIDEO({videoId,pageNum,pageSize})
        if(result && result.status ===0){
          var {videoUrl,commentListVO} = result.data
          if(!isGetComment){
            this.video = result.data
            this.videos = []
            //看看是否有多个视频，因为视频间用，隔开，所以要分开存入数组
            if(videoUrl.indexOf(',') !== -1){
              this.videos = videoUrl.split(',')
            }else{
              this.videos.push(videoUrl)
            }
          }
          this.comments = commentListVO
        }
      },

      //发表评论
      async uploadComment(){
        var {videoId} = this
        var {commentName,commentContent} = this.commentform
        if(commentName.trim() && commentContent.trim() && videoId){
          var typeId = videoId
          var result = await UPLOADCOMMENT({commentName,commentContent,typeId})
          if(result && result.status === 0){
            this.commentform.commentName = ''
            this.commentform.commentContent = ''
            var message = '发表成功，等待审核'
            this.message(message,'success',1500)
          }
        }else{
          this.message('请输入完整信息','warning',1500)
        }
      },

      //显示视频介绍
      setIntroduce(id,videoIntro){
        $('#'+id).html(videoIntro)
      },

      //消息提示
      message(message,type,duration){
        this.$message({
          message,
          type,
          duration
        })
      }
    },
    mounted () {
      if(this.$route.query.videoId){
        this.videoId = this.$route.query.videoId
        //更新菜单选中项
        this.choiceMenuId('Videos')
        this.choiceTypeMenuId('Videos')
        //获取视频详情
        this.reqVideo()
      }else{
        this.$router.replace({name:'Videos'})
      }
    },
  }
</script>

<style lang="less">
  @media (any-hover: hover) {
    .myDetail{
      .videoLists{
        .videoOuter{
          &:hover{
            padding-top: 10px;
            padding-bottom: 30px;
          }
        }
      }
    }
  }
  .userResource{
    .rightContent{
      .navTile{
        padding-left: 40px;
        height: 45px;
        border-radius: 5px 5px 0 0;
        line-height: 45px;
        color: #fff;
        font-size: 14px;
        background: rgb(85,171,235);
        overflow: hidden;
        a{
          color: white;
          text-decoration: none;
          &:hover{
            text-decoration: underline;
          }
        }
      }
      .content{
        line-height: 24px;
        padding: 10px 15px 15px;
        border: 1px solid #CECECE;
        border-top: 0;
        .myDetail{
          .videoTitle{
            text-align: center;
            margin: 15px 0;
            .videoP{
              font-size: 18px;
              font-weight: bold;
              color: #333333;
              line-height: 30px;
              margin-bottom: 0;
              margin-top: 0px;
            }
            .videoSpanDiv{
              line-height: 30px;
              border-bottom: 1px solid rgb(233,233,233);
              text-align: center;
              padding-bottom: 10px;
              margin-bottom: 20px;
              .videoSpan{
                display: inline-block;
                width: 100px;
                font-size: 12px;
                color: rgb(100,100,100);
              }
            }
          }
          .videoDiv{
            width: 80%;
            max-height: 350px;
            margin: 10px auto;
          }
          .videoContent{
            min-height: 150px;
            .videoContentTitle{
              font-size: 18px;
              font-weight: bold;
              color: #333333;
              margin-top:20px;
            }
            .videoIntroduce{
              padding: 15px;
              color: #666;
              font-size: 14px;
              line-height: 23px;
              min-height: 100px;
            }
          }
          .videoComment{
            .videoCommentItem{
              margin-top:35px;
              .videoCommentTitle{
                font-size: 18px;
                font-weight: bold;
                color: #333333;
                padding-bottom: 15px;
                border-bottom: 1px solid rgb(233,233,233);
              }
              .videoCommentForm{
                margin-top: 30px;
              }
              .videoComments{
                min-height: 200px;
                ul.commentUl{
                  padding-left: 0px;
                  padding-bottom: 20px;
                  border-bottom: 1px solid rgb(233,233,233);
                  margin: 10px 0;
                  li{
                    list-style: none;
                    padding: 0;
                    margin: 0;
                  }
                  .commenterInfo{
                    line-height: 30px;
                    font-size: 14px;
                    padding: 10px 5px;
                    &:before,&:after{
                      content: '';
                      display: table;
                      clear: both;
                    }
                    .commenterName{
                      display: inline-block;
                      float: left;
                    }
                    .commenterTime{
                      display: inline-block;
                      float: right;
                    }
                  }
                  .comment{
                    padding-left: 2em;
                    line-height: 30px;
                    font-size: 14px;
                    color: #333;
                    max-height: 120px;

                    /*最多显示四行内容，多出来的显示为...*/
                    overflow: hidden;
                    text-overflow: ellipsis;
                    display: -webkit-box;
                    -webkit-line-clamp: 4;
                    -webkit-box-orient: vertical;
                  }
                }
              }
              .emptyComment{
                padding: 30px 0 0;
                line-height: 30px;
                text-align: center;
                font-size: 20px;
                color: rgb(211,211,211);
              }
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 767px){
    .userResource{
      .rightContent{
        .navTile{
          padding-left: 10px;
        }
        .content{
          padding-top: 5px;
          .myDetail{
            .videoTitle{
              .videoP{
                font-size: 16px;
                margin-top: 0;
              }
            }
          }
        }
      }
    }
  }
  @media screen and (max-width: 499px){
    .userResource{
      .rightContent{
        .content{
          .myDetail{
            .videoDiv{
              width: 100%;
            }
          }
        }
      }
    }
  }
</style>



